.navbar {
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
  
.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    padding-right: 20px;
}

@import url('https://fonts.googleapis.com/css2?family=Alice&display=swap');

.nav-menu {
    background-color: #420057;
    font-family: 'Alice', serif;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}
  
.nav-menu.active {
    left: 0;
    transition: 350ms;
}
  
.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}
  
.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
  
.nav-text a:hover {
    background-color: #1a83ff;
}
  
.nav-menu-items {
    width: 100%;
    padding: 0;
    margin: 0;
}
  
.navbar-toggle {
    background-color: #420057;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
  
span {
    margin-left: 16px;
}